import React from 'react';
import { Link } from 'react-router-dom';

const ArticleList = ({articles}) => (
    <>
    {articles.map((item, key) => (
        <Link to={`/article/${item.name}`} key={key}>
            <h3>{item.name}</h3>
            <p>{item.content[0].substring(0,50)}...</p>
        </Link>
    ))}
    </>
);

export default ArticleList;
