import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Container from '@material-ui/core/Container';
import StarIcon from '@material-ui/icons/Star';

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
    marginTop: 200
  },
  button: {
    marginRight: theme.spacing(1),
  },
  instructions: {
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),
  }  
}));

const styles = {
    star: {
        color: "#f50057",
        position: "relative",
        top: 4
    }
}

function getSteps() {
  return ['infySEC Solutions Pvt. Ltd', 'Contus Support Interactive Pvt. Ltd', 'Trainer', 'Visteon Corporation'];
}

const InfySec = () => {
    return (<> <StarIcon style={styles.star} /><h4>Outstanding Performer Award - 2017</h4></>);
}

const Contus = () => {
    return (<> <StarIcon style={styles.star} /><h4>Star Performer Award - 2018</h4></>);
}

const Trainer = () => {
    return (<> <StarIcon style={styles.star} /><h4>ReactJS Trainer for freshers at Contus Support Interactive Pvt. Ltd</h4></>);
}

const Visteon = () => {
    return (<> <StarIcon style={styles.star} /><h4>Excellent Performace Award - 2019</h4></>);
}

function getStepContent(step) {
  switch (step) {
    case 0:
      return <InfySec />;
    case 1:
      return <Contus />;
    case 2:
      return <Trainer />;
    case 3:
      return <Visteon />;
    default:
      return 'Unknown step';
  }
}

export default function Archievements() {
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(0);
  const [skipped, setSkipped] = React.useState(new Set());
  const steps = getSteps();

  const isStepSkipped = step => {
    return skipped.has(step);
  };

  const handleNext = () => {
    let newSkipped = skipped;
    if (isStepSkipped(activeStep)) {
      newSkipped = new Set(newSkipped.values());
      newSkipped.delete(activeStep);
    }

    setActiveStep(prevActiveStep => prevActiveStep + 1);
    setSkipped(newSkipped);
  };

  const handleBack = () => {
    setActiveStep(prevActiveStep => prevActiveStep - 1);
  };

    const handleReset = () => {
    setActiveStep(0);
  };

  return (
    <div className={classes.root}>
        <Container maxWidth="md">
      <Stepper activeStep={activeStep}>
        {steps.map((label, index) => {
          const stepProps = {};
          const labelProps = {};
          if (isStepSkipped(index)) {
            stepProps.completed = false;
          }
          return (
            <Step key={label} {...stepProps}>
              <StepLabel {...labelProps}><h4>{label}</h4></StepLabel>
            </Step>
          );
        })}
      </Stepper>
      <div>
        {activeStep === steps.length ? (
          <div>
            <Typography className={classes.instructions}>
              <h4> Eagerly Waiting for NEW Archievements... </h4>
            </Typography>
            <Button onClick={handleReset} className={classes.button}>
              <h4>Reset</h4>
            </Button>
          </div>
        ) : (
          <div>
            <Typography className={classes.instructions}>{getStepContent(activeStep)}</Typography>
            <div>
              <Button disabled={activeStep === 0} onClick={handleBack} className={classes.button}>
                Back
              </Button>
              <Button
                variant="contained"
                color="primary"
                onClick={handleNext}
                className={classes.button}
              >
                {activeStep === steps.length - 1 ? 'Finish' : 'Next'}
              </Button>
            </div>
          </div>
        )}
      </div>
      </Container>
    </div>
  );
}
