import React from 'react';
import { withStyles, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Container from '@material-ui/core/Container';

const StyledTableCell = withStyles(theme => ({
  head: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);

const StyledTableRow = withStyles(theme => ({
  root: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.background.default,
    }    
  },
}))(TableRow);

function createData(name, calories, fat, carbs) {
  return { name, calories, fat, carbs };
}

const rows = [
  createData('Master of Computer Application', 'Erode Sengunthar Engineering College', '2010-2013', '82%' ),
  createData('B.Sc (Computer Science)', 'Karuppannan Mariappan College of Arts and Science', '2007-2019', '66%'),
  createData('12th', 'Govt. Hr. Sec. School', '2006-2007', 77),
  createData('10th', 'Govt. Boys Sec. School', '2004-2005', 72)  
];



const useStyles = makeStyles({
  table: {
    minWidth: 700,
    marginTop:200
  },
});

export default function Education() {
  const classes = useStyles();

  return (
    <Container maxWidth="lg" className={classes.table}>
      <Table className={classes.table} aria-label="customized table">
        <TableHead>
          <TableRow>
            <StyledTableCell>School/Degree</StyledTableCell>
            <StyledTableCell>School/College Name</StyledTableCell>
            <StyledTableCell>Duration Year</StyledTableCell>
            <StyledTableCell>Percentage</StyledTableCell>            
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map(row => (
            <StyledTableRow key={row.name}>
              <StyledTableCell component="th" scope="row">
                {row.name}
              </StyledTableCell>
              <StyledTableCell>{row.calories}</StyledTableCell>
              <StyledTableCell>{row.fat}</StyledTableCell>
              <StyledTableCell>{row.carbs}</StyledTableCell>              
            </StyledTableRow>
          ))}
        </TableBody>
      </Table>
      </Container>
  );
}
