import React from 'react';

const handleComments = (props) => {
    return props.comments.map((item, index) => {
        return <li key={index}>
                <h3>{item.username}</h3>
                <p>{item.comment}</p>
            </li>
    });
}

const CommentsComponent = (props) => {
 return(<div>
        <h3>Comments</h3>
        <ul>
            {handleComments(props)}
        </ul>
    </div>);
}

export default CommentsComponent;