import React, { useState, useEffect } from 'react';
import articles from './article-content';
import ArticleList from '../components/ArticleList';
import CommentsComponent from './CommentsComponend';

const ArticlePageComponent = ({match}) => {

    const name = match.params.name;
    const article = articles.find(item => item.name === name);
    const otherArticles = articles.filter(item => item.name !== name);

    const [articleInfo, setArticleInfo] = useState({upvotes:0, comments:[]});

    useEffect(() => {
        const fetchData = async () => {
            let response = await fetch(`/api/articles/${name}`);
            console.log(JSON.stringify(response));
            let result = await response.json();
            setArticleInfo(result);
        }
        fetchData();
    }, [name]);

    if(!article) return <h1>Article doesn't exist.</h1>

    return (
        <>
            <p>Article Upvotes Value: {articleInfo.upvotes}</p>
            <h1>Currenty Article Title - {article.name}</h1>
            {article.content.map((item, key) => {
                return <p key={key}>{item}</p>
            })}
            <div>
                <ArticleList articles={otherArticles} />
                <CommentsComponent comments={articleInfo.comments} />
            </div>
        </>
    )
}

export default ArticlePageComponent;