import React from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import HomePageComponent from './Pages/HomePageComponent';
import AboutPageComponent from './Pages/AboutPageComponent';
import ArticlePageComponent from './Pages/ArticlePageComponent';
import ArticlesListPageComponent from './Pages/ArticlesListPageComponent';
import PageNotFoundComponent from './Pages/PageNotFoundComponent';
import Technology from './Pages/Technology';
import Experience from './Pages/Experience';
import Archievements from './Pages/Archievements';
import Education from './Pages/Education';
import Navbar from './Navbar';
import './App.css';

function App() {
  return (
    <Router>
      <div className="App">
        <Navbar />
        <div id="page-body">
          <Switch>
            <Route path="/" component={HomePageComponent} exact />
            <Route path="/technology" component={Technology} />
            <Route path="/experience" component={Experience} />
            <Route path="/archievements" component={Archievements} />
            <Route path="/education" component={Education} />
            <Route path="/about" component={AboutPageComponent} />
            <Route path="/articles-list" component={ArticlesListPageComponent} />
            <Route path="/article/:name" component={ArticlePageComponent} />
            <Route component={PageNotFoundComponent}/>
          </Switch>
        </div>
      </div>
    </Router>
  );
}

export default App;
